.pic-container {
	width: 100%;
	height: 620px;
	position: absolute;
	top: 200px;
	#leftArrow {
		position: absolute;
		width: 64px;
		height: 64px;
		left: 6%;
		top: 50%;
		z-index: 2;
	}
	#rightArrow {
		position: absolute;
		width: 64px;
		height: 64px;
		right: 6%;
		top: 50%;
		z-index: 2;
	}
	.pic-wrapper {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		width: 1159px;
		height: 573px;
		background-color: #c4c4c4;
		overflow: hidden;
		.slider {
			height: 100%;
			width: 100%;
			display: flex;
			transition: transform 0.4s ease-in-out;
			transform: translateX(-1159px);
		}
		img {
			width: 100%;
			height: 100%;
		}
	}
	.dot-wrapper {
		margin-top: 42px;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		display: flex;
		.pic-dot {
			height: 15px;
			width: 15px;
			border-radius: 15px;
			background-color: black;
			margin: 5px;
			opacity: 0.2;
		}
	}
}
