.nav-container {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 100;
	width: 100%;
	min-width: 1080px;
	height: 167px;
	transition: 0.5s;
	background: white;
	display: flex;
	align-items: center;
	.nav-symbol {
		position: absolute;
		width: 320px;
		height: 60px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		left: 10%;
	}
	.nav-logo {
		width: 62px;
		height: 60px;
	}
	.nav-title {
		width: 239px;
		height: 40px;
		font-family: Comfortaa;
		font-style: normal;
		font-weight: bold;
		font-size: 18px;
		line-height: 20px;

		/* orange */
		color: #f26941;
	}
	.nav-bar {
		position: absolute;
		width: 711px;
		height: 30px;
		left: 44.17%;
		display: flex;
		justify-content: space-between;
		margin-top: 5px;
		.nav-link {
			font-family: Comfortaa;
			font-style: normal;
			font-weight: 500;
			font-size: 18px;
			line-height: 20px;
			color: #000000;
			text-decoration: none;
		}
		.nav-link-active {
			font-weight: bold;
			border-bottom: 2px solid #000;
		}
	}
}
.nav-scroll {
	height: 100px;
	box-shadow: 0 2px 5px #999999;
}
